




























































import { BASE_IMAGE_URL,getOrders} from "@/utils/api";

export default {
  data(){
	  return{
		  currentTab: 0,
		  list: [],
		  dai_pay_icon: BASE_IMAGE_URL+"dai_pay_icon.png",
		  finish_icon: BASE_IMAGE_URL+"finish_icon.png",
	  }
    
  },

  methods: {
    changeType: function(index){
      console.log(index);
      this.currentTab = index;
      this.$forceUpdate();
    },
    async getOrders(){
      let res = await getOrders();
      if(res.code == 200){
        this.list = res.data;
      }
    },

    next: function(item){
      let index = item.type;
      if(item.status == 1){
        //未付款
        if(index == 1){
          //里程车险
          const url = "../pay_address/main?id="+item.id;
          wx.navigateTo({ url });
        }else{
          //交强险
          wx.showModal({
            title: '提示',
            content: '请先支付里程车险，再支付交强险,现在为您跳转',
            success: function (res) {
                if (res.cancel) {
                  //点击取消,默认隐藏弹框
                } else {
                  //点击确定
                  const url = "../pay_all/main?id="+item.id;
                    wx.navigateTo({ url });
                }
            }
          });
        }

      }else{
        //已付款
        if(index == 1){
          //里程车险
          const url = "../order_one_detail/main?id="+item.id;
          wx.navigateTo({ url });
        }else{
          //交强险
          const url = "../order_two_detail/main?id="+item.id;
          wx.navigateTo({ url });

        }

        
      }
    }

  },

  onLoad () {
    this.getOrders();
  },


}
